import {Component} from "react";
import Movies from "../components/Movies";
import Prelouder from "../components/Prelouder";
import Search from "../components/Search";

const API_KEY = process.env.REACT_APP_API_KEY

class Main extends Component {
    constructor(props) {
        super(props);
        this.state = {
            movies: [],
            loading: true,
            name: 'terminator',
            type: ''
        }
    }

    componentDidMount() {
        fetch(`https://www.omdbapi.com/?apikey=81b708e8&s=terminator`)
            .then(res => res.json())
            .then(data => this.setState({movies: data.Search, loading: false }))
            .catch(err => {
                console.error(err);
                this.setState({loading: false})
            })

    }


    updateNameMovie = (name, e) => {
        this.setState({loading: true})
        e.preventDefault()
        this.setState({name: name})
        fetch(`https://www.omdbapi.com/?apikey=81b708e8&s=${name}`)
            .then(res => res.json())
            .then(data => this.setState({movies: data.Search, loading: false }))
            .catch(err => {
                console.error(err);
                this.setState({loading: false})
            })

    }

    updateType = (type, e) => {
        this.setState({loading: true})
        e.preventDefault();
        this.setState({type: type});
        fetch(`https://www.omdbapi.com/?apikey=81b708e8&s=${this.state.name}&type=${type}`)
            .then(res => res.json())
            .then(data => this.setState({movies: data.Search, loading: false }))
            .catch(err => {
                console.error(err);
                this.setState({loading: false})
            })

    }


    render() {
        const {movies, loading} = this.state;
        console.log(loading)

        return (
            <div className={'container content indigo lighten-5'}>

                <Search updateNameMovie={this.updateNameMovie} updateType={this.updateType}/>
                {loading ? <Prelouder/> :
                <Movies updateNameMovie={this.updateNameMovie} movies={this.state.movies}/>
                }
            </div>
        )
    }
}

export default Main;