import React from "react";
import './App.css';
// import AppFunc from "./funcProject/AppFunc";
import FirsPage from "./mainPage/FirsPage";

import {BrowserRouter as Router, Route, Routes} from "react-router-dom";
import AppMoviesProject from "./moviesProject/AppMoviesProject";
import AppMarvel from "./marvel/components/app/AppMarvel";
import {AppRest} from "./sushi/AppRest";
import BioPage from "./mainPage/BioPage";
import ContacsPage from "./mainPage/ContacsPage";
import {ComicsPage} from "./marvel/components/pages";
import {SingleComicPage} from "./marvel/components/pages";
import SingleCharPage from "./marvel/components/pages/SingleCharPage";
import AppSushi from "./sushi/AppSushi";
import AppSpaceX from "./star/AppSpaceX";

function App() {
    return (
        <Router>
            <Routes>
                <Route path='/' element={<FirsPage/>}/>
                <Route path='/movies' element={<AppMoviesProject/>}/>
                <Route path='/marvel' element={<AppMarvel/>}/>
                <Route path='/marvel/comics' element={<ComicsPage/>}/>
                <Route path='/marvel/comics/:id' element={<SingleComicPage/>}/>
                <Route path='marvel/:charName' element={<SingleCharPage/>}/>
                <Route path='/sushi' element={<AppSushi/>}/>
                <Route path='/bio' element={<BioPage/>}/>
                <Route path='/contacts' element={<ContacsPage/>}/>
                {/*<Route path='/spacex' element={<AppSpaceX/>}/>*/}
                <Route path='*' element={<FirsPage/>}/>

            </Routes>
        </Router>

    );
}

export default App;
